* {
  box-sizing: border-box;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen,
    Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
}

#search-box {
  font: inherit;
  padding: 8px;
  max-width: 320px;
  margin-bottom: 16px;
}

img {
  margin-right: 10px;
  margin-bottom: 10px;
}
.country-list {
  list-style: none;
}

.list {
  display: flex;
}

.list-info {
  list-style: none;

}

.current-country{
  display: flex;
  font-size: 22px;
  font-weight: 700;
}

.list-item{
  list-style: none;
  font-weight: 700;
}



